.search-container {
  display: flex;
  flex-flow: column nowrap;
  font-size: 0.75rem;
  width: 100%;
}
.search-fileds {
  border: none;
}
.search-form {
  border-radius: 50px;
  background-color: var(--wheat);
  box-shadow: 0px 1px 5px var(--blue-light);
  height: 2.5rem;
  width: 50%;
  max-width: 584px;
  margin: auto;
  position: relative;
}
.searchbar-legend,
.search-input-lable {
  visibility: hidden;
  position: absolute;
}
.searchbar-background {
  background: transparent;
}
.search-button {
  height: 100%;
  width: 8%;
  max-width: 50px;
  margin: 0.15rem;
  display: flex;
  float: left;
  justify-content: center;
  vertical-align: middle;
  align-items: center;
  background: transparent;
  cursor: pointer;
  border: none;
}
.search-button i {
  color: var(--blue-light);
  font-size: 1.5rem;
}
.search-input {
  font-size: 1rem;
  width: 80%;
  padding: 0.5rem 0;
  border: none;
}
.search-input::-webkit-search-cancel-button {
  display: none;
  -webkit-appearance: none;
  margin: 0;
}

.search-input:focus {
  outline: none;
}
.loader-icon {
  display: flex;
  flex-direction: row-reverse;
  margin-right: 0.5rem;
  width: 5%;
  float: right;
  right: -33.2rem;
  top: -1.8rem;
  flex: 1 0 auto;
  cursor: pointer;
  position: relative;
  border: none;
}
@media screen and (max-width: 768px) {
  .search-form {
    width: 65%;
  }
}

@media screen and (max-width: 600px) {
  .search-form {
    width: 80%;
  }
  .search-button {
    height: 50%;
    width: 3%;
    margin: 3px 5px;
  }
  .search-input {
    margin-left: 5px;
    margin-top: 0px;
  }
}
