.search-history {
  display: block;
  position: relative;
  width: 48%;
  max-width: 584px;
  height: 55vh;
  max-height: 500px;
  margin: auto;
  margin-top: 3rem;
  padding-left: 0.5rem;
}
.history-header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: baseline;
  padding: 0 0.5em;
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: var(--blue-dark);
}
.history-title {
  font-size: 0.9rem;
}
.clear-history {
  font-family: inherit;
  font-size: 0.9rem;
  font-weight: 700;
  text-decoration: underline;
  background: var(--blue-dark);
  border: none;
  cursor: pointer;
}
.history-list-container {
  display: flex;
  flex-flow: column nowrap;
  overflow: auto;
  width: 100%;
  max-height: 250px;
  margin: 0rem;
}
html ::-webkit-scrollbar {
  width: 0.75rem;
  height: 0.75rem;
}
html ::-webkit-scrollbar-track {
  background: var(--blue-pale);
}
html ::-webkit-scrollbar-thumb {
  background: var(--blue-dark);
  border-radius: 5rem;
}
.history-pane {
  margin-top: -0.1rem;
  padding-left: 0rem;
  height: 100%;
}
.history-item {
  display: flex;
  flex-flow: row nowrap;
  align-items: baseline;
  justify-content: space-between;
  background: var(--blue-darkest);
  box-shadow: 0px 5px 5px var(--blue-pale);
  padding: 0rem 0.7em;
}
.history-item:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.history-item:hover {
  background: var(--blue-pale);
  box-shadow: 0px 0px 0px var(--blue-pale);
}
.history-item-word {
  font-size: 0.8rem;
  font-weight: 700;
  cursor: pointer;
}
.history-item-date {
  font-size: 0.8rem;
  margin-left: auto;
  white-space: nowrap;
}
.delete-history-item {
  color: var(--blue-darker);
  margin-right: -6px;
  background: transparent;
  border: none;
  cursor: pointer;
  margin-left: 0.5rem;
}

.empty-history-text {
  display: block;
  width: 40%;
  margin: 1rem auto;
  font-size: 1rem;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .search-history {
    width: 65%;
  }
}
@media screen and (max-width: 600px) {
  .search-history {
    width: 75%;
  }
}
