.App-header {
  text-align: center;
}
.header-title {
  display: inline-block;
  font-weight: bold;
  font-size: 2.5rem;
  background-image: linear-gradient(
    90deg,
    #9400d3,
    #4b0082,
    #0000ff,
    #069e06,
    #ffff00,
    #ff7f00,
    #ff0000
  );
  color: var(--black);
  -webkit-background-clip: text;
  background-clip: text;
  white-space: nowrap;
}
@media screen and (max-width: 600px) {
  .header-title {
    font-size: 2rem;
    margin-left: 2rem;
  }
}
