* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: Tahoma, sans-serif;
  font-size: 100%;
  background-image: url("../public/image/background.jpg");
  background-attachment: fixed;
  background-size: cover;
}
:root {
  --black: #0000;
  --wheat: #ffffff;
  --blue: #aef3ec;
  --blue-border: #6495ed;
  --blue-pale: #5f9ea0;
  --blue-light: #10c4f1;
  --blue-dark: #07b2b8;
  --blue-darker: #04858a;
  --blue-darkest: #06caca4d;
}
.search-app {
  min-height: 500px;
  min-width: 400px;
}
