.suggested-words-container {
  z-index: 99;
  display: flex;
  flex-direction: column;
  width: 50%;
  max-width: 584px;
  margin: auto;
}
.suggested-words-list {
  display: block;
  margin-top: 0.2rem;
}
.suggested-words-item {
  padding-top: 0.1rem;
  padding-left: 1rem;
  line-height: 0.2rem;
  list-style: none;
  margin-left: -2.5rem;
  font-size: 1rem;
  border-radius: 50px;
  background: var(--blue);
  border-bottom: 0.1px solid var(--blue-border);
  cursor: pointer;
}
.suggested-words-item:hover {
  background: var(--blue-dark);
}
.suggested-words-item.active {
  background: var(--blue-dark);
}
.suggested-words-item:hover .suggested-words-item.active {
  background: var(--blue);
}
.word-letters {
  font-size: 1rem;
  line-height: 0.2em;
  text-align: left;
}

.word-letters-highlight {
  font-weight: bolder;
}

@media screen and (max-width: 768px) {
  .suggested-words-container {
    width: 65%;
  }
}

@media screen and (max-width: 600px) {
  .suggested-words-container {
    width: 80%;
    min-width: 330px;
  }
}
